* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  width: 100%;
  padding-left: 50px;
  padding-right: 50px;
  font-family: Roboto, serif;
  font-size: 12px;
  line-height: 14px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.image {
  justify-self: start;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-top: 10px;
  display: flex;
}

.nav {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  display: block;
}

.nav__item:nth-child(n+2) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
